.swiper {
  position: relative;
  --swiper-theme-color: #000000 !important;
  display: flex;
  overflow-y: hidden;
  overflow-x: scroll;
  scroll-behavior: smooth;
  height: 100vh;
  .swiper-button-prev {
    left: 70px !important;
    top: 80%;
    color: #000000 !important;
  }
  .swiper-button-next {
    right: 50% !important;
    top: 80%;
    color: #000000 !important;
  }
  .swiper-horizontal > .swiper-pagination-bullets,
  .swiper-pagination-bullets.swiper-pagination-horizontal,
  .swiper-pagination-custom,
  .swiper-pagination-fraction {
    bottom: 70px !important;
  }
  .sub_container_section {
    position: relative;
    width: 100% !important;
    height: 100vh !important;
    article {
      position: absolute;
      top: -50px;
      width: 100%;
      height: 100vh;
      .sub_inner {
        .sub_number {
          z-index: 2;
          position: absolute;
          left: 40%;
          top: 219px;
          font-weight: bolder;
          font-size: 200px;
          line-height: 242px;
          text-align: center;
          text-shadow: -17px 18px 4px rgba(0, 0, 0, 0.25);
        }
      }
      .sub_line {
        position: absolute;
        width: 522px;
        height: 3px;
        right: 30%;
        top: 60%;
        background-color: #000000;

        box-shadow: -15px 16px 4px rgba(0, 0, 0, 0.25);
        transform: rotate(90deg);
      }
      .sub_circle {
        z-index: 0;
        position: absolute;
        width: 650px;
        height: 650px;
        left: 169px;
        top: 219px;
        border-radius: 50%;
        background: linear-gradient(
          227.02deg,
          rgba(248, 248, 248, 0) 9.35%,
          rgba(122, 122, 122, 0)
        );
        box-shadow: -16px 19px 18px -5px rgba(0, 0, 0, 0.25);
      }
      .sub_video1 {
        box-sizing: border-box;
        z-index: 1;
        position: absolute;
        width: 650px;
        height: 330px;
        left: 74px;
        top: 400px;

        border: 14px solid rgb(0, 0, 0);
        backdrop-filter: blur(2px);
        /* Note: backdrop-filter has minimal browser support */
        border-radius: 43px;

        overflow: hidden;
        video {
          width: 100%;
        }
      }
      .sub_video2 {
        box-sizing: border-box;

        position: absolute;
        width: 300px;
        height: 571px;
        left: 284px;
        top: 366px;
        backdrop-filter: blur(2px);
        /* Note: backdrop-filter has minimal browser support */
        border: 14px solid rgb(0, 0, 0);

        overflow: hidden;
        border-radius: 43px;
        video {
          width: 100%;
        }
      }

      .sub_content {
        padding: 30px 25px;
        gap: 25px;

        position: absolute;
        right: 200px;
        top: 300px;

        background: rgba(255, 255, 255, 0.2);
        box-shadow: inset 0px 4px 4px rgba(0, 0, 0, 0.25);
        backdrop-filter: blur(2px);
        border-radius: 10px;
        h3 {
          font-weight: 900;
          font-size: 48px;
          span {
            position: relative;
            top: -10px;
            padding: 10px;
            margin-left: 10px;
            width: 48px;
            height: 30px;
            font-size: 10px;
            background: #000000;
            border-radius: 5px;
            color: #fff;
          }
        }
        .sub_date {
          font-weight: bold;
          margin: 10px 0;
        }
        .sub_lang {
          display: flex;
          flex-direction: row;
          padding: 0;
          margin: 10px 0 40px;
          li {
            padding: 12px;
            font-size: 10px;
            color: #fff;
            background-color: #000000;
            border-radius: 5px;
            margin-right: 10px;
          }
        }
        .sub_cont {
          display: flex;
          flex-direction: row;
          justify-content: space-between;
          align-items: center;
          padding: 0;
          .sub_cont_title {
            font-size: 20px;
            font-weight: 700;
          }
          .sub_cont_text {
            font-size: 12px;
            line-height: 300%;
            li > h4 {
              font-weight: 700;
            }
          }
        }
      }
    }
    .sub_btn {
      position: absolute;
      right: 200px;
      bottom: 100px;
      .sub_btn_1 {
        display: flex;
        flex-direction: row;
        button {
          a {
            text-decoration: none;
            color: #fff;
          }
          cursor: pointer;
          margin-left: 10px;
          padding: 10px;
          background: #000000;
          border-radius: 5px;
          color: #fff;
        }
      }
      .sub_btn_2 {
        margin-top: 10px;
        display: flex;
        flex-direction: row;
        button {
          a {
            text-decoration: none;
            color: #fff;
          }
          cursor: pointer;
          margin-left: 10px;
          padding: 10px;
          background: #000000;
          border-radius: 5px;
          color: #fff;
        }
      }
    }
  }

  .sub__bg {
    position: fixed;
    z-index: -1;
    top: 0;
    width: 100%;
    height: 100%;
    background: conic-gradient(
        from 207.88deg at 58.47% 83.98%,
        #afafaf 0deg,
        rgba(231, 231, 231, 0) 360deg
      ),
      #ffffff;
    filter: blur(1px);
  }
}
