/* 다리 */
.legs1 {
  position: absolute;
  z-index: 1;
  top: 190px;
  left: 40px;
  width: 25px;
  height: 25px;
  border-radius: 0 0 50% 50%;
  box-shadow: -30px 0 0 #bcdebb, 30px 0 0 #bcdebb;
  animation: legs1-ani 1s linear 0s infinite;
}
@keyframes legs1-ani {
  0% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(5px);
  }
  50% {
    transform: translateY(0px);
  }
  75% {
    transform: translateY(5px);
  }
  100% {
    transform: translateY(0px);
  }
}
.legs2 {
  position: absolute;
  z-index: 1;
  top: 190px;
  left: 70px;
  width: 25px;
  height: 25px;
  border-radius: 0 0 50% 50%;
  box-shadow: -30px 0 0 #bcdebb, 30px 0 0 #bcdebb;
  animation: legs1-ani2 1s linear 0s infinite;
}
@keyframes legs1-ani2 {
  0% {
    transform: translateY(5px);
  }
  100% {
    transform: translateY(0px);
  }
  50% {
    transform: translateY(5px);
  }
  75% {
    transform: translateY(0px);
  }
  100% {
    transform: translateY(5px);
  }
}
